import populateRegisteredData from '../helpers/populateRegisteredData'
import LocalStorageData from '../services/LocalStorageData';

const initialStateUser = {
  isLogedIn: false,
  uid: null,
  stsTokenManager: null,
  isAnonymous: true,
  displayName: '',
  email: '',
  data: {},
  phoneNumber: null,
  photoURL: null,
  providerId: null,
  providerData: [],
  metadata: {},
};

const user = (state = initialStateUser, action) => {
  // eslint-disable-next-line default-case
  switch (action.type) {
    case 'SET_USER_DATA':
      const newState = populateRegisteredData(state, action.value, initialStateUser);
      if (newState.uid) newState.isLogedIn = true;
      state = newState;
      LocalStorageData.setItem('userData', state);
      break;
  }
  return state;
};

export default user;
