import { connect } from 'react-redux';
import { Navigate } from 'react-router-dom';
import BaseForm from '../components/base/Form';
import FirebaseAuth from '../services/FirebaseAuth';

import FormInputField from '../components/commons/FormInputField';

class LoginPage extends BaseForm {
  firebaseAuth = new FirebaseAuth();

  state = {
    email: '',
    password: '',
    user: {},
  };

  handleLogin = async () => {
    await this.firebaseAuth.loginEmailPassword(this.state);
  };

  render() {
    return (
      <div className="min-h-screen bg-slate-200 py-6 flex flex-col justify-center relative overflow-hidden sm:py-12">
        {this.props.user.isLogedIn ? <Navigate to="/" /> : ''}
        <span className="border text-4xl text-black px-6 pt-10 pb-8 bg-white w-1/2 max-w-md mx-auto rounded-t-md sm:px-10">
          Wellcome
        </span>
        <div className="border relative px-4 pt-7 pb-8 bg-white shadow-xl w-1/2 max-w-md mx-auto sm:px-10 rounded-b-md">
          <FormInputField
            label="Email"
            name="email"
            type="email"
            placeholder="your-email@domain.com"
            autoComplete="username"
            value={this.props.email}
            onChange={this.handleChange}
          />
          <FormInputField
            label="Password"
            name="password"
            type="password"
            placeholder="****"
            autoComplete="current-password"
            value={this.props.password}
            onChange={this.handleChange}
          />
          {/*
              <div className="flex items-start">
                <div className="flex items-start">
                  <div className="flex items-center">
                    <input
                      id="remember"
                      aria-describedby="remember"
                      type="checkbox"
                      className="bg-gray-50 border border-gray-300 focus:ring-3 focus:ring-blue-300 h-4 w-4 rounded dark:bg-gray-700 dark:border-gray-600 dark:focus:ring-blue-600 dark:ring-offset-gray-800"
                      required=""
                    />
                  </div>
                  <div className="text-sm ml-3">
                    <label htmlFor="remember" className="font-medium text-gray-900">
                      Remember me
                    </label>
                  </div>
                </div>
                <a
                  href="#"
                  className="text-sm text-blue-700 hover:underline ml-auto dark:text-blue-500"
                >
                  Lost Password?
                </a>
              </div>
              */}
          <button
            className="mt-5 bg-green-500 hover:bg-blue-700 shadow-xl text-white uppercase text-sm font-semibold px-14 py-3 rounded"
            onClick={this.handleLogin}
          >
            Login
          </button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ user }) => {
  return { user };
};

export default connect(mapStateToProps)(LoginPage);
