import parseToRawObject from './parseToRawObject';

const populateRegisteredData = (state, newState, defaultState) => {
  let newStateData = { ...state };
  if (newState && Object.keys(newState).length) {
    newState = parseToRawObject(newState);
    Object.keys(state).forEach((key) =>
      typeof newState[key] != 'undefined'
        ? (newStateData[key] = newState[key])
        : ''
    );
  } else newStateData = defaultState;
  console.log('populateRegisteredData', newStateData);
  return newStateData;
};

export default populateRegisteredData;
