import {
  getAuth,
  onAuthStateChanged,
  signInWithEmailAndPassword,
  signOut
} from 'firebase/auth';
import store from '../store';

class FirebaseAuth {
  auth = getAuth();

  getUserLogin = async () => {
    await onAuthStateChanged(this.auth, (user) => {
      if (user) {
        store.dispatch({
          type: 'SET_USER_DATA',
          value: user,
        });
      } else {
        store.dispatch({
          type: 'SET_USER_DATA',
          value: null,
        });
      }
    });
  };

  loginEmailPassword = async ({ email, password } = {}) => {
    await signInWithEmailAndPassword(this.auth, email, password)
      .then((userCredential) => {
        // Signed in
        const user = userCredential.user;
        store.dispatch({
          type: 'SET_USER_DATA',
          value: user,
        });
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = error.message;
        store.dispatch({
          type: 'SET_USER_DATA',
          value: null,
        });
        console.log(errorCode, errorMessage);
      });
  };

  logout = async () => {
    await signOut(this.auth);
    store.dispatch({
      type: 'SET_USER_DATA',
      value: null,
    });
  }
}

export default FirebaseAuth;
