import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import FirebaseAuth from '../services/FirebaseAuth';

class Navbar extends Component {
  firebaseAuth = new FirebaseAuth();

  render() {
    let authButton = (
      <Link
        to="/login"
        className="text-2xl no-underline text-grey-darkest hover:text-blue-dark px-2"
      >
        Login
      </Link>
    );

    if (this.props.user?.isLogedIn)
      authButton = (
        <button
          onClick={this.firebaseAuth.logout}
          className="text-2xl no-underline text-grey-darkest hover:text-blue-dark px-2"
        >
          Logout
        </button>
      );

    return (
      <nav className="font-sans flex flex-col text-center sm:flex-row sm:text-left sm:justify-between py-4 px-6 bg-white shadow sm:items-baseline w-full">
        <div className="mb-2 sm:mb-0">
          <Link
            to="/"
            className="text-2xl no-underline text-grey-darkest hover:text-blue-dark px-2"
          >
            Home
          </Link>
          <Link
            to="/about"
            className="text-2xl no-underline text-grey-darkest hover:text-blue-dark px-2"
          >
            About
          </Link>
        </div>
        <div>
          {this.props.user?.isLogedIn ? (
            <Link
              to="/profile"
              className="text-2xl no-underline text-grey-darkest hover:text-blue-dark px-2"
            >
              {this.props.user?.displayName || this.props.user?.email}
            </Link>
          ) : (
            ''
          )}
          {authButton}
        </div>
      </nav>
    );
  }
}

const mapStateToProps = ({ user }) => {
  return { user };
};

export default connect(mapStateToProps)(Navbar);
